<script>
    import TimelineComponent from './components/TimelineComponent';
    import LinkComponent from './components/LinkComponent';
    import DataService from './services/dataService';
    import Vue from 'vue';
    import html2canvas from 'html2canvas';
    // import jsPDF from 'jsPDF';

    Vue.prototype.$dataService = new DataService();
    export default {
        name: 'App',
        components: {
            // MissionComponent,
            LinkComponent,
            TimelineComponent
        },
        data: function () {
            const self = this;
            const skills = ['js', 'angular2', 'react', 'vuejs', 'nodejs', 'git', 'css3', 'html5', 'windows', 'linux', 'typescript']
                .map(key => {
                    return {
                        key,
                        note: self.$dataService.getSkill(key),
                    };
                });

            return {
                skills,
                miscs: this.$dataService.getMiscs(),
                trainings: this.$dataService.getTrainings(),
                personnalStuff: this.$dataService.getPersonalStuffs()
            }
        },
        methods: {
            exportPdf: () => {
                const filename = 'myCV.pdf';
                window.console.log('Exporting...');
                html2canvas(document.getElementById('app')).then(function (canvas) {
                    window.console.log('Export pdf ok!');
                    const imgData = canvas.toDataURL('image/png');

                    /*
                    Here are the numbers (paper width and height) that I found to work.
                    It still creates a little overlap part between the pages, but good enough for me.
                    if you can find an official number from jsPDF, use them.
                    */
                    let imgWidth = 210;
                    let pageHeight = 295;
                    let imgHeight = canvas.height * imgWidth / canvas.width;
                    let heightLeft = imgHeight;

                    let doc = new window.jsPDF('p', 'mm', 'a4', true);
                    let position = 0;
                    const resolution = 'SLOW';
                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight, '', resolution);
                    heightLeft -= pageHeight;

                    while (heightLeft >= 0) {
                        position = heightLeft - imgHeight;
                        doc.addPage();
                        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight, '', resolution);
                        heightLeft -= pageHeight;
                    }
                    doc.save(filename);
                });
            }
        }
    }
</script>
