            <!--<template v-slot:opposite :class="`${employer.color}&#45;&#45;text`">-->

            <div class="py-3">
                <h2 :class="`headline font-weight-light mb-3 ${employer.color}--text`">

                    <v-layout row>
                        <v-flex xs5>
                            <LinkComponent v-if="employer.companyKey" v-bind:theKey="employer.companyKey" class="headline font-weight-bold"/>
                            <!--<span-->
