                        <!--{{obj.txt}}</span>-->

                        <!--</v-flex>-->
                        <v-flex  xs3 :class="`duration ${employer.color}--text`">{{employer.duration}}</v-flex>
                    </v-layout>
                    <div class="capacity">{{employer.capacity}}
                        <div class="employer-skills">
                            <span v-for="(skill, i) in getSkills(employer.missions).skills" v-bind:key="i">
                                <LinkComponent v-bind:theKey="skill" :displayName="false"/>
                            </span>
