                        {{mission.companyKey ? '-':''}}</b>
                    {{mission.capacity}}
                    <span class="duration2">({{mission.duration}})</span>
                    <div v-for="(practice,j) in mission.practices" :key="j" class="practice">
                        <span class="dot"></span>{{practice}}
                    </div>
