<script>
    import LinkComponent from './LinkComponent';

    const skillsLinkKeys = ['js', 'angular2', 'react', 'vuejs', 'nodejs', 'git', 'cs', 'typescript'/*, 'css3'*/];
    const sortWithOrder = (order) => {
        return function (a, b) {
            var indexA = order.indexOf(a);
            var indexB = order.indexOf(b);
            if (indexA < 0 && indexB < 0) return -1;
            if (indexA < 0) return 1;
            if (indexB < 0) return -1;
            return indexA - indexB;
        };
    }

    export default {
        name: 'linkComponent',
        props: ['data'],
        components: {LinkComponent},
        methods: {
            formatDate: function (str = '') {
                return str.split(' ').map(x => {
                    return {txt: x, isNb: /\d/.test(x)}
                });
            },
            getSkills: function (missions) {
                let skills = [];
                const keywords = [];
                missions.forEach(mission => {
                    skills = mission.skillsKeys.concat(skills);
                });
                skills = [...new Set(skills)];
                skills = skills
                    .filter(x => skillsLinkKeys.includes(x))
                    .sort(sortWithOrder(skillsLinkKeys));
                return {
                    skills: [...new Set(skills)],
                    keywords
                }
            }
        },
        data: function () {
            return {
                employers: this.$dataService.getEmployers()
            }
        }
    }
</script>

